'use client';
import React from 'react';

// Fallback to native scrolling behavior to resolve the missing dependency error.
// The "scroll-smooth" class in your layout.tsx will still handle basic smooth scrolling.
export default function SmoothScroll({ children }: { children: React.ReactNode }) {
  return (
    <div style={{ width: '100%', height: '100%' }}>
      {children}
    </div>
  );
}